.button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 56px;
  width: 100%;
  background-color: #fff;

  cursor: pointer;

  color: $c-gray;
  border-radius: 40px;
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;

  @include hover(box-shadow, 1px 4px 16px rgba(232, 232, 240, 0.4));

  &:active {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
  }

}
